import React from 'react';

const FeedbackOptions = ({ goodHandleIncrement, neutralHandleIncrement, badHandleIncrement }) => (
    <div>
        <button 
            type ="button" 
            onClick={goodHandleIncrement}
            > 
            Good
        </button>

        <button 
            type ="button" 
            onClick={neutralHandleIncrement}
            > 
            Neutral
        </button>

        <button 
            type ="button" 
            onClick={badHandleIncrement}
            >
            Bad 
        </button>
    </div>
);

export default FeedbackOptions;