import React from 'react';
import FeedbackOptions from './components/FeedbackOptions/FeedbackOptions';
import Statistics from './components/Statistics/Statistics';
import Section from './components/Section/Section';
import Notification from './components/Notification/Notification';

class App extends React.Component {
    state = {
        good: 0,
        neutral: 0,
        bad: 0
    }

    goodHandleIncrement = () => {
        this.setState(prevState => ({
            good: prevState.good + 1,                
        }));
    };

    neutralHandleIncrement = () => {
        this.setState(prevState => ({
            neutral: prevState.neutral + 1,                           
        }));
    };

    badHandleIncrement = () => {
        this.setState(prevState => ({
            bad: prevState.bad + 1,                           
        }));
    }

    countTotalFeedback = () => {
        return this.state.good + this.state.neutral + this.state.bad;
    };

    countPositiveFeedbackPercentage = () => {
        return Math.round((this.state.good * 100)/(this.state.good + this.state.neutral + this.state.bad))        
    }

    render(){
        const total = this.countTotalFeedback();
        const positivePercentage = this.countPositiveFeedbackPercentage();
        return (
        <div>
            <Section text="Please leave feedback">
              
              <FeedbackOptions 
                goodHandleIncrement = {this.goodHandleIncrement} 
                neutralHandleIncrement={this.neutralHandleIncrement} 
                badHandleIncrement={this.badHandleIncrement} 
               />
            </Section>
            
            <Section >
            <Statistics 
                good={this.state.good} 
                neutral={this.state.neutral} 
                bad={this.state.bad} 
                total={total} 
                positivePercentage={positivePercentage}
            /> 
            </Section>

            <Notification 
                message="No feedback given"
            />
        </div>
        )    
    }
}

export default App;